.blog {
  background-color: #ffefe7;
  padding-bottom: 70px;
  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__title {
    position: relative;
    padding: 25px 63px;
    font-weight: 700;
    font-size: 32px;
    line-height: 139%;
    text-align: center;
    letter-spacing: 0.03em;
    color: #bd957e;
    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: #bd957e;
    }
  }
  &__desc {
    max-width: 727px;
    margin: 56px 0;
    font-family: "Playfair";
    font-style: normal;
    font-weight: 700;
    font-size: 48px;
    line-height: 139%;
    text-align: center;
    letter-spacing: 0.02em;
    color: #795744;
    @include tablet {
      font-size: 38px;
    }
  }
  &__cards {
    margin-top: 33px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 33px;
    @include tablet {
      grid-template-columns: repeat(2, 1fr);
    }
    @include small-tablet {
      grid-template-columns: 1fr;
    }
    @include mobile {
      padding: 0 5px;
    }
  }
  &__card {
    display: flex;
    align-items: center;
  }
  .card__desc {
    margin: 0 0 0 32px;
    font-weight: 400;
    font-size: 20px;
    line-height: 171.5%;
    letter-spacing: 0.02em;
    color: #333333;
    @include big-desktop {
      margin: 0 0 0 12px;
    }
  }
}
