.hero {
  margin-top: 85px;
  display: flex;
  &__left {
    margin-right: 100px;
    img {
      height: 100%;
      object-fit: cover;
      width: 100%;
      @include tablet {
        display: none;
      }
    }
    @include tablet {
      margin-right: 0;
    }
  }
  &__right {
    margin-top: 140px;
    max-width: 603px;
    margin-bottom: 20px;
    @include big-desktop {
      margin-top: 80px;
    }
    @include tablet {
      margin: 0 auto 30px auto;
    }
    @include mobile {
      margin: 0 10px 20px 10px;
    }
    &-title {
      font-family: "Playfair";
      font-style: normal;
      font-weight: 700;
      font-size: 56px;
      line-height: 139%;
      letter-spacing: 0.02em;
      color: #e5a985;
      @include mobile {
        font-size: 40px;
      }
    }
    &-text {
      font-size: 24px;
      line-height: 171%;
      letter-spacing: 0.02em;
      color: $text-color;
      @include mobile {
        font-size: 20px;
      }
    }
  }
  &__form {
    display: flex;
    &-label {
      margin-right: 15px;
    }
    &-input {
      background: #e5e5e5;
      border-radius: 8px 0px 0px 8px;
      padding: 15px 390px 15px 30px;
      &::placeholder {
        color: #c4c4c4;
      }
      &:focus {
        outline: none;
      }
      @include big-desktop {
        padding: 15px 140px 15px 30px;
      }
      @include tablet {
        padding: 15px 100px 15px 20px;
      }
      @include mobile {
        padding: 15px 60px 15px 20px;
      }
    }
    &-btn {
      min-width: 160px;
      padding: 8px 22px;
      font-weight: 700;
      font-size: 20px;
      color: #ffffff;
      background: #91916c;
      border-radius: 0px 8px 8px 0px;
      @include big-desktop {
        font-size: 14px;
        padding: 8px 12px;
        min-width: 70px;
      }
      @include mobile {
        font-size: 12px;
      }
    }
  }
}
