.contact {
  margin-top: 85px;
  padding-bottom: 90px;
  @include tablet {
    padding-bottom: 0;
  }
  &-top {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  &__title {
    position: relative;
    margin-top: 0;
    margin-bottom: 85px;
    padding: 25px 63px;
    font-weight: 700;
    font-size: 32px;
    line-height: 139%;
    text-align: center;
    letter-spacing: 0.03em;
    color: #bd957e;
    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: #bd957e;
    }
  }

  &__content {
    background-color: #91916c;
    color: #fff;
    position: relative;
    &-title {
      padding-top: 45px;
      font-family: "Playfair";
      font-weight: 700;
      font-size: 48px;
      line-height: 139%;
      text-align: center;
      letter-spacing: 0.02em;
      color: #ffffff;
      @include tablet {
        font-size: 38px;
      }
    }
    &-bottom {
      display: flex;
      justify-content: space-around;
      flex-wrap: wrap;
      padding-bottom: 40px;
    }
    &-img {
      margin-left: 100px;
      transform: translateY(120px);
      max-width: 100%;
      object-fit: cover;
      order: 2;
      @include big-desktop {
        transform: translateY(120px);
        margin-left: 20px;
      }
      @include tablet {
        display: none;
      }
    }
  }
}
