.swiper {
  position: relative;
  border-radius: 20px;
  &-wrapper {
    max-width: 800px;
  }
  &-btn-next {
    cursor: pointer;
    z-index: 13;
    width: 58px;
    height: 58px;
    position: absolute;
    background-image: url(../../img/svg/next-btn.svg);
    background-repeat: no-repeat;
    background-position: center;
    top: 42%;
    right: 0;
    @include small-tablet {
      background-image: none;
    }
    @include mobile {
      background-image: none;
    }
  }
  &-btn-prev {
    cursor: pointer;
    z-index: 13;
    width: 58px;
    height: 58px;
    position: absolute;
    background-image: url(../../img/svg/prev-btn.svg);
    background-repeat: no-repeat;
    top: 42%;
    left: 0;
    @include small-tablet {
      background-image: none;
    }
    @include mobile {
      background-image: none;
    }
  }
  &-slide {
    border-radius: 20px;
    img {
      border-radius: 20px;
    }
  }
}
