.services {
  padding-bottom: 64px;
  padding-top: 40px;
  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;

    @include small-tablet {
    }
  }
  &__title {
    position: relative;
    padding: 25px 63px;
    font-weight: 700;
    font-size: 32px;
    line-height: 139%;
    text-align: center;
    letter-spacing: 0.03em;
    color: #91916c;
    margin: 0;
    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: #91916c;
    }
  }
  &__desc {
    max-width: 727px;
    margin-top: 56px;
    margin-bottom: 110px;
    font-family: "Playfair";
    font-style: normal;
    font-weight: 700;
    font-size: 48px;
    line-height: 139%;
    text-align: center;
    letter-spacing: 0.02em;
    color: #e5a985;
    @include tablet {
      font-size: 38px;
      margin-bottom: 45px;
    }
  }
  &__cards {
    display: flex;
    gap: 26px;
    flex-wrap: wrap;
    justify-content: center;
  }
  &__card {
    background-color: #fae3d8;
    border-radius: 20px;
    max-width: 351px;
    &-desc {
      font-weight: 400;
      font-size: 24px;
      line-height: 138.5%;
      text-align: center;
      letter-spacing: 0.02em;
      color: #000000;
      padding: 25px 47px;
      margin: 0;
    }
    &:nth-child(2) {
      background-color: #91916c;
      .services__card-desc {
        color: #fff;
      }
    }
  }
}
