.product {
  padding-top: 95px;
  padding-bottom: 40px;
  background-color: #e1e1c2;
  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    @include tablet {
      padding: 0 10px;
    }
  }
  &__title {
    position: relative;
    padding: 25px 63px;
    font-weight: 700;
    font-size: 32px;
    line-height: 139%;
    text-align: center;
    letter-spacing: 0.03em;
    color: #91916c;
    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: #91916c;
    }
  }
  &__desc {
    max-width: 727px;
    margin: 56px 0;
    font-family: "Playfair";
    font-style: normal;
    font-weight: 700;
    font-size: 48px;
    line-height: 139%;
    text-align: center;
    letter-spacing: 0.02em;
    color: #65653e;
    @include tablet {
      font-size: 40px;
    }
  }
  &__bottom {
    margin-top: 30px;
    font-weight: 700;
  }
}
