.form {
  display: flex;
  flex-direction: column;
  gap: 27px;
  margin-top: 200px;
  z-index: 2;
  @include big-desktop {
    margin-top: 130px;
  }
  @include tablet {
    margin-top: 70px;
  }
  &__label {
    &-holder {
      display: flex;
      gap: 10px;
      @include tablet {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 27px;
      }
    }
  }
  &__input {
    padding: 15px 534px 15px 25px;
    font-weight: 400;
    font-size: 20px;
    line-height: 135.5%;
    color: #ffffff;
    background: rgba(225, 225, 194, 0.4);
    border-radius: 8px;
    &::placeholder {
      color: #fff;
    }
    &:focus {
      outline: none;
    }
    @include tablet {
      padding: 16px 134px 16px 25px;
    }
    @include mobile {
      padding: 16px 124px 16px 25px;
    }
    &-top {
      color: #ffffff;
      padding: 16px 134px 16px 25px;
      font-weight: 400;
      font-size: 20px;
      line-height: 135.5%;
      background: rgba(225, 225, 194, 0.4);
      border-radius: 8px;
      &:focus {
        outline: none;
      }
      &::placeholder {
        color: #fff;
      }
      @include mobile {
        padding: 16px 124px 16px 25px;
      }
    }
  }
  &__btn {
    padding: 15px 0;
    background: #e5a985;
    border-radius: 8px;
    font-weight: 700;
    font-size: 20px;
    line-height: 135.5%;
    color: #ffffff;
    width: 100%;
  }
}
