.header {
  &__content {
    padding-top: 50px;
    display: flex;
    justify-content: space-between;
    @include big-desktop {
      padding-left: 20px;
      padding-right: 20px;
    }
  }
}
.list {
  display: flex;
  gap: 76px;
  z-index: 13;
  transition: all 0.3s ease-in-out;
  @include tablet {
    position: fixed;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 30px;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100vh;
    background-color: #91916c;
    overflow-y: auto;

    transform: translateX(-1600px);
    &.active {
      transform: translateX(0px);
    }
  }
  &__link {
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    color: #000;
    position: relative;
    &::after {
      content: "";
      position: absolute;
      background: #000;
      height: 2px;
      width: 100%;
      bottom: 0;
      left: 0;
      transform: scale(0);
      @include transition;
    }
    &:hover::after {
      transform: scale(1);
    }
    @include tablet {
      font-size: 32px;
      color: #383824;
    }
  }
}
.burger {
  margin-right: 10px;
  opacity: 0;
  display: none;
  z-index: 13;
  width: 30px;
  height: 30px;
  position: relative;
  transition: all 0.3s ease-in-out;

  &__line {
    display: block;
    height: 2px;
    width: 30px;
    background-color: #383824;
    &::before {
      content: "";
      position: absolute;
      top: 6px;
      left: 0;
      display: block;
      height: 2px;
      width: 30px;
      background-color: #383824;
      transition: all 0.3s ease-in-out;
    }
    &::after {
      content: "";
      position: absolute;
      bottom: 6px;
      left: 0;
      display: block;
      height: 2px;
      width: 30px;
      background-color: #383824;
      transition: all 0.3s ease-in-out;
    }
  }
  @include tablet {
    opacity: 1;
    display: block;
  }
  &.active {
    .burger__line {
      width: 0;
      height: 0;
      transition: all 0.3s ease-in-out;
      &::before {
        transform: translate(0px, 8px) rotate(45deg);
      }
      &::after {
        transform: translate(0px, -7px) rotate(-45deg);
      }
    }
  }
}
